import { Component } from '@angular/core';
import {AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import {  renderDateTime} from 'ionic-angular/util/datetime-util';




@IonicPage()
@Component({
  selector: 'page-formulario-moviladd',
  templateUrl: 'formulario-moviladd.html',
})
export class FormularioMoviladdPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";

  idafiliado:any='na';
  formulario: any;
  nformulario: any;
  idempresa:any;
  cmpver:any=[];
  public hasFilter: boolean = false;
  permisom: any=[];
  idcat:any;
  idcatn:any;
  idfrm:any;
  idfrmn:any;
//private barcodeScanner:BarcodeScanner,
  constructor(   public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController, public navParams: NavParams,   public regService: Auth,
    private _MODAL  		: ModalController
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value


    })
  }
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()
  {


    var campo=''
    this.hasFilter = true;
    let select = {
      title: '¿Por que campo quiere buscar?',
       inputs: [],
      buttons: [           {            text: 'Cancelar'     },    {       text: 'Buscar',handler: todo => {
          //buscamos
          campo=todo

                    let select2 = {
                      title: 'Ingrese información a buscar',
                      inputs: [{
                        name: 'campo2',
                        label:'Buscar',
                        placeholder: ''
                      }],
                      buttons: [           {            text: 'Cancelar'     },    {       text: 'Buscar',handler: todo2 => {
                          //buscamos
                         //campo  y campo2


                         let self = this;
                         self.loading = true;
                         this.regService.getReg(this.formulario+ '/'+campo + '/'+todo2.campo2 + '/buscar' , 'frmmovils').then((data) => {
                           this.items=data;
                           self.loading = false;
                     }, (err) => {
                       this.msj.showError3('Error',err.error);
                      // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
                     });



                        }  } ]
                  }

                  let newAlert2 = this.alertCtrl.create(select2);
                  newAlert2.present();


        }  } ]
  }
  this.regService.getReg(this.formulario + '/formulariocamposver2/' + this.idempresa,'frmmovils').then((data) => {
    var aa:any;
    aa=data;

    for (var i = 0; i < aa.length; i++) {
      select.inputs.push({name: 'myInput', type: 'radio',label:aa[i].nombre,value:aa[i]._id});
  }
  let newAlert = this.alertCtrl.create(select);
  newAlert.present();
}, (err) => {
this.msj.showError3('Error',err.error);
});




  }
  keys:any=[];
  keys2:any;



  public getKeys3(data,op){
    var re=''
    if(data)
    {       for(var i = 0; i < data.length; i++) {
            if(data[i].name==op)
            {
                re=data[i].nombre;

            }

            }
    }
    else{re= ""}

return re;

  }



  public dahora(data){
    var re='';
    var aa=data.split('T')
    if(aa[0])
    {
    var bb=(aa[1]).split(':')
    if(bb[0])
    {
    re= renderDateTime("hh:mm A", {day: 1,
      hour: Number(bb[0]),
      millisecond: 0,
      minute:  Number(bb[1]),
      month: 1,
      second: 0,
      tzOffset: 0,
      year: 2019}, {dayNames: undefined,
        dayShortNames: undefined,
        monthNames: undefined,
        monthShortNames: undefined});

      }


    }
  //DD/MM/YYYY hh:mm A
  return re;

 }

  public dafecha(data){
  var  re=''
  if(data)
  {
    re=data.substr(0,10)
    var aa=re.split('-')
    if(aa[0])
    {
    re=aa[2] +'-' + aa[1] + '-'+aa[0]
    }
  }
   return re;
  }

  public dafechacompleta(data){
    var  re=''

      var aa=data.split('T')
      if(aa[0])
      {
      re=this.dafecha(aa[0]) +' ' + this.dahora(data)
      }
     return re;
    }

  public getKeys2(data){
    if(data)
    {      if(data.key)   {    return  data.label;   }  else  {   return data;  }
    }
    else{return ""}



  }
  public getKeys(data){
    var keyst:any=[];
   // var keys2:any=[];
    this.keys=Object.keys(data);
    var cadurs=''
    var cadurs2=''
    for(var i = 0; i < this.keys.length; i++) {
      var aa=this.keys[i]
      switch (aa) {
        case '_id':     break;
        case '_v':      break;
        case '__v':      break;
        case 'usuarionew':
        cadurs='Creación [' +  data[aa] + ' , '; break;
        case 'usuarioup':
        cadurs2='Actualiza [' +  data[aa] + ' , ';  break;
        case 'createdAt':
        cadurs=cadurs + '' +  (data[aa]).substring(0,10) + ']';  break;
        case 'updatedAt':
        cadurs2=cadurs2 + '' +  (data[aa]).substring(0,10) + ']';  break;
        default:

        var pone= this.getKeys3(this.cmpver,aa);
        if(pone!='')
        {//moment(date).format('DD-MM-YYYY');
              //var aareg:any=aa;
              var tcampo:any=''
              if(aa.includes('fechahora')){tcampo='fechahora'}
              else{
                if(aa.includes('fecha')){tcampo='fecha'}
                else{
                  if(aa.includes('hora')){tcampo='hora'}
                }

              }


              if(aa.includes('imagen')){tcampo='imagen'}

              switch (tcampo) {
                case 'fechahora':
                keyst.push('<strong>' +  this.getKeys3(this.cmpver,aa)  + '</strong> : ' + this.dafechacompleta(this.getKeys2(data[aa])));
                break;
                case 'fecha':
                   keyst.push('<strong>' +  this.getKeys3(this.cmpver,aa)  + '</strong> : ' + this.dafecha(this.getKeys2(data[aa])));
                   break;
                case 'hora':
                   keyst.push('<strong>' +  this.getKeys3(this.cmpver,aa)  + '</strong> : ' + this.dahora(this.getKeys2(data[aa])) );
                   break;
                   case 'imagen':
                   keyst.push('<strong>' +  this.getKeys3(this.cmpver,aa)  + '</strong> :  <img src="' +  this.getKeys2(data[aa]) + '"   class="circle-pic"  height="50 px" width="50 px"> '  );
                   break;
                default:
                keyst.push('<strong>' +  this.getKeys3(this.cmpver,aa)  + '</strong> : ' + this.getKeys2(data[aa]) );
              }

        }
        break
      }
    }

    keyst.push(cadurs + ' ' +cadurs2);
    this.keys=keyst
/*
    for(var i2 = 0; i2 < keyst.length; i2++) {
      keys2.push(keyst[i2])
    }
    keys2.push(cadurs + ' ' +cadurs2)
    this.keys=keys2*/
    return true;
  }
/*
  keys(obj){
    return Object.keys(obj);
}
*/
  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg(this.formulario+ '/formulario/' + this.idempresa, 'frmmovils').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }


  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {
    this.idcat=this.navParams.data.record2.idcat;
    this.idcatn=this.navParams.data.record2.idcatn;
		this.idfrm= this.navParams.data.record._id;
    this.idfrmn=this.navParams.data.record.nombre;
    this.formulario=this.navParams.data.record._id;
    this.nformulario=this.navParams.data.record.nombre;
   this.cmpver=this.navParams.data.record2.datat;
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }



  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {


    let recordID 		: string		= item._id;
    this.regService.deleteReg(recordID+'/' +this.idfrm + '/' +this.username+ '/' + this.idempresa + '/' + this.idafiliado,'frmmovils').then((data) => {
        this.items.splice(no, 1);
        this.items = this.items.concat([]);
      }, (err) => {
        this.msj.showError3('Error',err.error);
    });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('FormularioMovilPage', { record : item, record2:{idcat:this.idcat,idcatn:this.idcatn,idfrm:this.idfrm,idfrmn:this.idfrmn}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('FormularioMovilPage',{ record : null,record2:{idcat:this.idcat,idcatn:this.idcatn,idfrm:this.idfrm,idfrmn:this.idfrmn}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }




}



